import { mapGetters } from 'vuex'
export default {
  middleware: 'auth',
  async asyncData ({ $axios }) {
    try {
      let response = await $axios.$get(`${process.env.DEV_BACKEND}/api/orders`)

      if (response.success) {
        return {
          orders: response.products
        }
      }
    } catch (error) {
      console.log(error);
    }
  },

  methods: {
    ...mapGetters(['getLog', 'getCity', 'getCartLength']),
  }
}
